.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.navbar{

  background-color: #282c34;
  color: white;

}


.preview {

  position:relative;
  margin-left: auto;
  bottom: 1030px;
  

  width: 640px;
  height: 100px;
  border-color: #282c34;
  border: 100px;

 
}

.textbox {
  width: 700px;
  height: 1000px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: x-large;
  font-weight: 500;

}

.btn{
  color: white;
  background-color: #282c34;
  border: none;
  position: relative;
  left: 10px;
  width: 50px;
  height: 50px;
  font-size: larger;
}
.inp{
  border: none;
  background: #46484d;
  color: white;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: large;
  position: relative;
  left: 20px;

  bottom: 10px;
}
.save{
    color: white;
  background-color: #282c34;
  border: none;
  position: relative;
  left: 30px;
  width: 50px;
  height: 50px;
  font-size: larger;
  bottom: 10px;

}
